.wallet-modal-container {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateX(0px);
    animation: 0.3s ease-in-out 0s 1 normal forwards running opacity-animation;
    overflow-y: auto;
    padding-top: 3%;
    padding-bottom: 3%;
    z-index: 99999999999;
}

.wallet-modal-bg {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.7);
    z-index: 9998;
}

.wallet-modal-body {
    max-width: 500px;
    margin: auto;
    width: 100%;
    background: #141414;
    z-index: 9999;
    border-radius: 15px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    overflow: auto;
    .wallet-image{
        width: 25px;
        height: 25px;
        border-radius: 50%;
    }
}

.wallet-modal-header {
    background: #191727;
}

.modal-content {
    overflow: auto;
    height: 100%;
    max-height: 700px;
}

@media (max-width:600px) {
    .wallet-modal-body {
        width: 90%;
    }
}

// ------------ status modal --------------

.status-modal-container {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateX(0px);
    animation: 0.3s ease-in-out 0s 1 normal forwards running opacity-animation;
    overflow-y: auto;
    padding-top: 3%;
    padding-bottom: 3%;
    z-index: 99999999999;

    .modal-content {
        overflow: auto;
        height: 100%;
        max-height: 700px;
    }

    .status-modal-bg {
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        background: rgba(0, 0, 0, 0.7);
        z-index: 9998;
        color: black;
    }

    .status-modal-body {
        max-width: 800px;
        margin: auto;
        width: 100%;
        background: white;
        z-index: 9999;
        border-radius: 15px;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: space-between;
        overflow: auto;
    }

    .status-modal-header {
        position: relative;

        .status-close {
            position: absolute;
            right: 30px;
            top: 30px;
        }

        // background: #191727;
    }
}

