.faq-section {
    // background: #0F0F0F;
    color: black;

    /*  */
    .faq {
        width: 100%;
        max-width: 750px;
        margin: auto;
    }

    .faq .menu {
        display: block;
        margin: 0 auto;
        position: relative;
        list-style: none;
        width: 100%;
    }

    .faq .menu>li>a {

        color: black;
        font-weight: bold;
        cursor: pointer;
        display: block;
        text-align: center;
        text-decoration: none;
    }

    .faq .menu>ul {
        list-style-type: disclosure-closed;
    }

    .faq .menu ul {
        height: 0;
        opacity: 0;
        transition: all .5s ease;
        top: 35px;
        width: 100%;
        list-style: none;
    }

    .content-visable {
        height: 130px !important;
        opacity: 1 !important;
        transform: translateY(0) !important;
    }

    @media (max-width:576px) {
        .content-visable {
            height: 230px !important;
        }
    }

    .faq .menu ul a {
        color: #000;
        display: block;
        padding: 5px 20px;
    }

    .faq-title {
        // background: #212121;
        color: black !important;
        border-top: 1px solid black;
        // border-bottom: 1px solid black;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px 15px;
        cursor: pointer;
        // border-radius: 5px;
    }

    .faq-content {
        text-align: left;
        color: #000;
        padding: 5px 15px;
    }


}