.metamask-btn {
    border: 1px rgb(66, 206, 66) solid;
    border-radius: 10px;
    padding: 5px 15px;
    font-size: 15px;
}

header {
    z-index: 100;
    // border-bottom: 1px grey solid;
    padding: 10px 0px;
    position: fixed !important;
    left: 0;
    right: 0;
    padding: 10px 0px;
    color: black;
    // background: #1818189c;

    .header-container {
        position: relative;
        top: 0;
    }

    .language {
        width: 35px;
        cursor: pointer;

        &:hover {
            scale: 1.1;
        }
    }

    .hamburger {
        // margin-top: 1rem;
    }

    .launch-app-btn {
        // dis-f ai-c
        margin-left: 2rem;
        display: flex;
        gap: 2rem;
        align-items: center;
        justify-content: center;
    }

    .title {
        padding: 16px;
        color: #d3b16e;
    }

    .badge {
        background-color: #333023;
        color: #d3b16e;
        border-radius: 10px;
        padding: 2px 10px;
        margin-left: 20px;
    }

    ul li {
        padding: 0 10px;
        cursor: pointer;
    }

    ul li:hover {
        color: #FCBF07;
    }

    ul {
        margin-bottom: 0;
        padding: 0px;
    }

    .hamburger-icon-container {
        display: none;
        flex-direction: column;
        position: relative;
        width: 25px;
        height: 40px;
        z-index: 999;
        cursor: pointer;
    }

    .hamburger-icon-container:hover {
        color: #FCBF07;
    }

    .hamburger-icon {
        display: none;
    }

    .hamburger-bg {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 998;
        background: #181818;
        opacity: 0.6;
        display: block !important;
    }

    .hamburger-box {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        background: #181818;
        z-index: 999;
        flex-direction: column;
        padding: 20px;
        align-items: unset;
        justify-content: unset;
    }

    .hamburger-box ul li {
        font-size: 20px;
        margin-top: 10px;
    }

    .hamburger-box ul li:hover {
        // box-shadow: 0px 0px 10px #FCBF07;
        // border-radius: 5px;
        color: #FCBF07;
        transition: all .5s;
    }

    .hamburger-cancel-container {
        width: 100%;
        display: flex;
        justify-content: flex-end;
    }

    .hamburger-cancel-container .hamburger-cancel-btn:hover {
        color: #FCBF07;
        cursor: pointer;
    }

    .header-logo-container {
        .header-logo {
            display: flex;
            align-items: center;
            justify-content: start;
        }
    }


    @media (max-width:1280px) {
        .header-logo-container {
            position: absolute;
            left: 0;
            right: 0;
            display: flex;
            justify-content: center;

            .header-logo {
                margin: auto;
            }
        }

        .hamburger-icon-container {
            display: flex;
        }

        .hamburger {
            display: none;
        }

        .row-reverse-991 {
            flex-flow: row-reverse;
        }

        .hamburger-icon {
            display: inline;
        }

        .hamburger-box {
            right: 65%;
        }

        .hamburger-box .hamburger {
            display: inline !important;
        }

        .hamburger-icon-container {
            display: flex;
        }

        .hamburger-box {
            right: 0;
        }

        .hamburger-box .hamburger {
            display: inline !important;
        }

        .mobile-just-start {
            justify-content: start;
        }

    }

    @media (max-width:998px) {
        .header-logo-container {
            justify-content: start;

            .header-logo {
                margin-left: 35px;
            }
        }
    }

    @media (max-width:780px) {
        .header-logo-container {
            position: relative;
            left: 0;
            right: 0;
            display: flex;
            justify-content: center;

            .header-logo {
                margin: 0;

                svg {
                    width: 100%;
                }
            }
        }
    }

    @media (max-width:640px) {
        .launch-app-btn {
            // dis-f ai-c
            // position: absolute;
            // left: 0;
            // right: 0;
            gap: 1rem;
            margin-left: 1rem;

            .connect-wallet {
                margin: auto;
                position: absolute;
                top: 12px;
                right: 15%;
                // left: 0;
                // right: 0;
            }
        }
    }


    @media (max-width:600px) {

        .hamburger-icon-container {
            display: flex;
        }

        .hamburger-box {
            right: 0;
        }

        .hamburger-box .hamburger {
            display: inline !important;
        }

        .mobile-just-start {
            justify-content: start;
        }


    }

    @media (max-width:500px) {
        .launch-app-btn {
            gap: 1rem;
            margin-left: 1rem;

            .display-chain {
                display: none;
            }

            .connect-wallet {
                margin: auto;
                position: absolute;
                top: 12px;
                right: 15%;
            }
        }
    }

    @media (max-width:400px) {
        .launch-app-btn {
            margin-left: 0.5rem;
            gap: 0.5rem;

            .display-chain {
                display: none;
            }

            .connect-wallet {
                margin: auto;
                position: absolute;
                top: 12px;
                left: 35%;
            }
        }
    }

}

@media (max-width:600px) {
    header {
        position: fixed !important;
        left: 0;
        right: 0;
        padding: 15px 0px;
        background: #2c2c2c;
        border: none;

        .header-container {
            position: relative;
            top: 0;
        }
    }


}